import { _decorator, Component, Node, RichText, Label, Prefab, ScrollView, director } from 'cc';
const { ccclass, property } = _decorator;
import { AudioManager } from "../common/AudioManager";
import { Languages } from "../common/Language";
import * as i18n from "db://i18n/LanguageData";
@ccclass('Detail')
export class Detail extends Component {

    @property(RichText)
    richTextContentInfo: RichText |null =null;

    @property(Label)
    lblTitle: Label |null =null;

    @property(Label)
    lblTitleInfo: Label |null =null;

    @property(Node)
    content: Node = null;

    @property(Prefab)
    prefabTools: Prefab = null;
   
    @property(Prefab)
    prefabMainNumbers: Prefab = null;

    @property(Prefab)
    prefabPeriod: Prefab = null;

    @property(ScrollView)
    scrollView: ScrollView = null;

    @property(Node)
    bgInfo: Node = null;

    onLoad(){
        this.initLayout()
    }
    start() {

    }

    update(deltaTime: number) {
        
    }


    initLayout(){
        AudioManager.checkPlayMusic();


    }


    clickInfo(){
        AudioManager.playSoundClick();
        this.bgInfo.active==true? this.hideDialogInfo() : this.showDialogInfo()
    }

    hideDialogInfo(){

this.bgInfo.active=false
    }

    showDialogInfo(){
        this.bgInfo.active=true

    }

    loadHome(){
        AudioManager.playSoundClick();

        director.loadScene("Home")
    }

    loadPeriodCycle(){
        AudioManager.playSoundClick();

        director.loadScene("PeriodCycle")
    }
}

 